import React from 'react';
import { withStyles } from '@material-ui/core/styles'

import ErrorIcon from '@material-ui/icons/Error'
import Typography from '@material-ui/core/Typography';

import red from '@material-ui/core/colors/red'

const styles = theme => ({
  danger: {
    color: red[700]
  },
  advice: {
    color: red[500]
  },
  message: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    height: '80vh'
  }  
})

const NoFoundPage = (props) => {
  const {classes} = props
  return (
    <div className={classes.message}>
      <Typography align='center' className={classes.danger} variant='display1'>
        <ErrorIcon /> Page not found
        <Typography align='center' variant='title' className={classes.advice}>
          <br />Please select a tab one the navbar
        </Typography>
      </Typography>
    </div>
  )
}

export default withStyles(styles)(NoFoundPage)


